import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
// import { Work } from '../data/formData.model';
import { FormDataService } from '../data/formData.service';
import { MyserviceService } from '../myservice.service';
import { Work } from '../data/formData.model';

@Component ({
    selector:     'mt-wizard-work',
    templateUrl: './work.component.html'
})

export class WorkComponent implements OnInit {

    title = 'Configuration Details';
    workType: Work;
    form: any;

    // hostname = '';
    // ipaddress = '';
    email = '';
    // Memory = '';
    public memory = 1024;
    loading = false;

    // shareHost() {
    //   return this.hostname;
    // }

    onMemorySelect(value) {
      this.memory = value;
      console.log(this.memory);
    }

    // selected20(hostname: any) {
    //   this.hostname = hostname;
    //   console.log(hostname);
    // }

    // selected21(ipaddress: any) {
    //   this.ipaddress = ipaddress;
    //   console.log(ipaddress);
    // }

    // testing multi component
    @ViewChild('nameRef') nameRef: ElementRef;
    Hname: string;


    constructor(private router: Router, private formDataService: FormDataService, private appService: MyserviceService) {

    }

    // onSubmit() {
    //   this.appService.MyHost(this.hostname);
    // }

    // testing multi component

    ngOnInit() {
      this.workType = this.formDataService.getWork();
      console.log('Personal feature loaded!');
    }

    save(form: any): boolean {
      if (!form.valid) {
        return false;
      }

      this.formDataService.setWork(this.workType);
      return true;
    }

    goToPrevious(form: any) {
      if (this.save(form)) {
          // Navigate to the personal page
          this.router.navigate(['/personal']);
      }
  }

    goToNext(form: any) {
      if (this.save(form)) {
        // Navigate to the work page
        this.router.navigate(['/address']);
        // this.appService.MyHost(this.hostname);
      }
      this.loading = true;
      console.log(this.memory);
      console.log(this.email);
    }
  }



