import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Deploy } from '../data/formData.model';
import { FormDataService } from '../data/formData.service';
import { MyserviceService } from '../myservice.service';


@Component({
  selector: 'mt-wizard-deploy',
  templateUrl: './deploy.component.html',
  styleUrls: ['./deploy.component.css']
})
export class DeployComponent implements OnInit {

  title = 'Target Deployment';
  deployType: string;
  form: any;
  radio = '';

  onRadioButtonClick(value) {
    this.radio = value;
     console.log (this.radio);
  }

  @ViewChild('nameRef') nameRef: ElementRef;
  Hname: string;

  constructor(private router: Router, private formDataService: FormDataService, private appService: MyserviceService)
      {
        appService.myhost.subscribe((data: string) => {
          this.Hname = data;
     });
    }

  ngOnInit() {
    this.deployType = this.formDataService.getDeploy();
    console.log('Deploy feature loaded!');
}

save(form: any) {
    // if (!form.valid) {
    //     return false;
    // }

    this.formDataService.setDeploy(this.deployType);
    return true;
}

  goToPrevious(form: any) {
    if (this.save(form)) {
        this.router.navigate(['/address']);
    }
}

goToNext(form: any) {
    if (this.save(form)) {
        this.router.navigate(['/result']);
    }
}

}



