import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Address } from '../data/formData.model';
import { FormDataService } from '../data/formData.service';
import { MyserviceService } from '../myservice.service';

@Component ({
    selector: 'mt-wizard-address',
    templateUrl: './address.component.html'
})

export class AddressComponent implements OnInit {
    title = 'Applications';
    address: Address;
    form: any;

    java_version = 'undefined';
    mysql = 'undefined';
    mongodb = 'undefined';
    spark = 'undefined';
    Python = 'udefined';
    cassandra = 'undefined';
    hbase = 'undefined';
    hadoop = 'undefined';

    selected1(java_version: any) {
      this.java_version = java_version;
      console.log (this.java_version);
    }

    selected2(mysql: any) {
      this.mysql = mysql;
      console.log (this.mysql);
    }

    selected3(mongodb: any) {
      this.mongodb = mongodb;
      console.log (this.mongodb);
    }

    selected4(spark: any) {
      this.spark = spark;
      console.log (this.spark);
    }

    selected5(python: any) {
      this.Python = python;
      console.log (this.Python);
    }

    selected6(cassandra: any) {
      this.cassandra = cassandra;
      console.log (this.cassandra);
    }

    selected7(hbase: any) {
      this.hbase = hbase;
      console.log (this.hbase);
    }

    selected8(hadoop: any) {
      this.hadoop = hadoop;
      console.log (this.hadoop);
    }



    @ViewChild('nameRef') nameRef: ElementRef;
    Hname: string;

    constructor(private router: Router, private formDataService: FormDataService, private appService: MyserviceService) {
      appService.myhost.subscribe((data: string)=>{
        this.Hname = data;

      });
    }

    ngOnInit() {
        this.address = this.formDataService.getAddress();
        console.log('Address feature loaded!');
    }

    save(form: any) {
        if (!form.valid) {
            return false;
        }

        this.formDataService.setAddress(this.address);
        return true;
    }

    goToPrevious(form: any) {
        if (this.save(form)) {
            // Navigate to the work page
            this.router.navigate(['/work']);
        }
    }

    goToNext(form: any) {
        if (this.save(form)) {
            this.router.navigate(['/deploy']);
        }
    }
}



