import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Personal } from '../data/formData.model';
import { FormDataService } from '../data/formData.service';
import { MyserviceService } from '../myservice.service';

@Component({
  selector: 'mt-wizard-personal',
  templateUrl: './personal.component.html'
})

export class PersonalComponent implements OnInit {
  title = 'Operating System';
  personalType: string;
  form: any;
  radio = '';

    onRadioButtonClick(value) {
      this.radio = value;
       console.log (this.radio);
    }

    @ViewChild('nameRef') nameRef: ElementRef;
    Hname: string;

    constructor(private router: Router, private formDataService: FormDataService, private appService: MyserviceService) {
      appService.myhost.subscribe((data: string)=>{
        this.Hname = data;

      });
    }

    ngOnInit() {
        this.personalType = this.formDataService.getPersonal();
        console.log('Work feature loaded!');
    }

    save(form: any): boolean {
        if (!form.valid) {
            return false;
        }

        this.formDataService.setPersonal(this.personalType);
        return true;
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the address page
            this.router.navigate(['/work']);
            // this.appService.MyHost(this.Ubuntu);
            // this.appService.MyHost(this.CentOS);
            // this.appService.MyHost(this.RedHat);
            // this.appService.MyHost(this.Windows);
        }

    }
}




