import { NgModule }           from '@angular/core';
import { BrowserModule }      from '@angular/platform-browser';
import { FormsModule }        from '@angular/forms';

/* App Root */
import { AppComponent }       from './app.component';
import { NavbarComponent }    from './navbar/navbar.component';

/* Feature Components */
import { PersonalComponent }  from './personal/personal.component';
import { WorkComponent }      from './work/work.component';
import { AddressComponent }   from './address/address.component';
import { DeployComponent }   from './deploy/deploy.component';
import { ResultComponent }    from './result/result.component';
import { HttpClientModule } from '@angular/common/http';

/* Routing Module */
import { AppRoutingModule }   from './app-routing.module';

/* Shared Service */
import { FormDataService }    from './data/formData.service';
import { WorkflowService }    from './workflow/workflow.service';
import { MyserviceService } from './myservice.service';
import { FooterComponent } from './footer/footer.component';
import { OnpremisesComponent } from './onpremises/onpremises.component';
import { CloudComponent } from './cloud/cloud.component';
import { NavproComponent } from './navpro/navpro.component';

@NgModule({
    imports:      [ BrowserModule,
                    FormsModule,
                    AppRoutingModule,
                    HttpClientModule
                  ],
    providers:    [{ provide: FormDataService, useClass: FormDataService },
                   { provide: WorkflowService, useClass: WorkflowService },
                  MyserviceService
                  ],
    declarations: [ AppComponent, NavbarComponent, PersonalComponent, WorkComponent, AddressComponent, DeployComponent, ResultComponent, FooterComponent, OnpremisesComponent, CloudComponent, NavproComponent ],
    bootstrap:    [ AppComponent ]
})

export class AppModule {}



